.search-container {
  display: flex;
  align-items: center;
  border: none;
  width: 250px;
  margin-bottom: calc(var(--margin-box));
}

.search-container input {
  border: 1px solid #ccc;
  outline: none;
  width: 100%;
  height: 40px;
  font-size: 14px;
  border-radius: var(--border-radius-small);
}

@media (max-width: 950px) {
  .search-container {
    width: 40%;
  }
}
