.expandable-container {
  width: 100%;
  background: var(--color-box-bg);
  border-radius: var(--border-radius-small);
  padding: var(--margin-box);
  margin-top: var(--margin-box);
  border: 1px solid var(--color-border);
}

.expandable-container-faq {
  width: 100%;
  background: var(--secondary-color);
  color: var(--color-white);
  border-radius: var(--border-radius-small);
  padding: var(--margin-box);
  margin-top: var(--margin-box);
  border: 1px solid var(--color-border);
}

.expandable-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  padding: var(--margin-box);
}

.arrow {
  font-size: 20px;
  transition: transform 0.3s ease-in-out;
}

.arrow.open {
  transform: rotate(180deg);
}

.expandable-content {
  overflow: hidden;
  max-height: 0;
  opacity: 0;
  padding: var(--margin-box);
  transition: max-height 0.5s ease-in-out, opacity 0.3s ease-in-out;
}

.white-text {
  color: var(--color-white);
  font-size: 14px;
}

.white-icon {
  filter: invert(99%) sepia(7%) saturate(321%) hue-rotate(284deg)
    brightness(114%) contrast(88%);
}

.expandable-content.show {
  max-height: 300px;
  opacity: 1;
}

.deadline {
  display: flex;
  justify-content: start;
  align-items: center;
  width: 40%;
  gap: calc(var(--gap) / 2);
}

.icon-clock {
  width: 36px;
  padding: var(--margin-box);
  background-color: rgb(146, 149, 135);
  border-radius: var(--border-radius-small);
  filter: invert(100%) sepia(4%) saturate(753%) hue-rotate(261deg)
    brightness(116%) contrast(80%);
}

.priority-type-div {
  border-radius: var(--border-radius-small);
  padding: var(--margin-box) var(--margin);
  width: 10%;
  display: flex;
  justify-content: center;
}

.priority-type-div p {
  color: white;
}

.high {
  background-color: rgb(218, 95, 95);
}

.medium {
  background-color: rgb(220, 135, 65);
}

.done_type-div {
  display: flex;
  justify-content: start;
  align-items: center;
  width: 15%;
  gap: calc(var(--gap) / 4);
}

.not_done {
  color: chocolate;
}

.done {
  color: var(--primary-color);
}

.flex-horizontal {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.flex-horizontal-start {
  display: flex;
  justify-content: start;
  align-items: center;
  gap: calc(var(--gap) / 2);
}

.toggle-switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
}

.toggle-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  border-radius: 20px;
  transition: 0.3s;
}

.slider::before {
  position: absolute;
  content: "";
  height: 14px;
  width: 14px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  border-radius: 50%;
  transition: 0.3s;
}

input:checked + .slider {
  background-color: var(--primary-color);
}

input:checked + .slider::before {
  transform: translateX(20px);
}

@media (max-width: 950px) {
  .priority-type-div {
    display: none;
  }

  .done_type-div {
    display: none;
  }

  .deadline {
    width: 80%;
  }
}
