.logo-menu-div {
  display: none;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 10px;
  z-index: 101;
}

.logo-menu-div h2 {
  font-size: 20px;
  color: var(--color-black);
}

.logo-menu-div strong {
  color: var(--primary-color);
}

.logo-menu-div img {
  width: 24px;
  cursor: pointer;
}

.header-div {
  position: fixed;
  background-color: var(--color-box-bg);
  padding: var(--margin);
  border-radius: var(--border-radius-small);
  width: calc(100% - var(--menu-width) - (2 * var(--margin)));
  border: 1px solid var(--color-border);
  box-shadow: -10px -80px 0 rgb(255, 255, 255), 30px -80px 0 rgb(255, 255, 255),
    -10px 10px 0px rgba(255, 255, 255, 0.984), 30px 10px 0px rgb(255, 255, 255);
  z-index: 100;
}

.header-title-div {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.notification-div {
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--color-black);
  border-radius: var(--border-radius-small);
  cursor: pointer;
}

.notification-div img {
  width: 32px;
  align-items: center;
  padding: var(--margin-box);
}

.dropdown-menu-a {
  position: absolute;
  top: 100%;
  left: 0;
  background-color: var(--color-black);
  color: white;
  border-radius: var(--border-radius-small);
  width: 100%;
  padding: calc(var(--margin) / 1.5);
  list-style: none;
  z-index: 1001;
}

.menu-btn {
  background-color: white;
  border: none;
}

.dropdown-item-a {
  padding: var(--margin-box);
  cursor: pointer;
}

.dropdown-item-a:hover {
  background: var(--secondary-color);
  border-radius: calc(var(--border-radius-small) - var(--margin-box));
}

.menu-info {
  display: flex;
  align-items: center;
  justify-content: end;
}

.dd-menu-line {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-block: 4px;
  font-size: 14px;
}

.dd-menu-line div {
  display: flex;
  align-items: center;
  gap: var(--margin-box); 
}

.dd-menu-line img {
  height: 18px;
  width: 18px;
}

.dd-menu-arrow{
  filter: invert(100%);
}

@media (max-width: 950px) {
  .total-header {
    display: flex;
    align-items: center;
    justify-content: space-around;
  }

  .logo-menu-div {
    display: flex;
    width: 92%;
    background-color: white;
    padding: 10px;
    box-shadow: -20px -20px 0 rgb(255, 255, 255),
      40px -20px 0 rgb(255, 255, 255),
      -100px 10px 0px rgba(255, 255, 255, 0.984),
      30px 10px 0px rgb(255, 255, 255);
  }

  .header-div {
    position: absolute;
    width: 92%;
    top: 70px;
  }
}
