.columns-50 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: var(--gap);
}

.col-50 {
  width: 50%;
}

.timeline-route {
  display: flex;
  align-items: center;
  gap: 12px;
  background-color: var(--color-box-bg);
  padding: var(--padding);
  border-radius: var(--border-radius-small);
  height: 155px;
  border: 1px solid var(--color-border);
}

.timeline-route img {
  width: 18px;
  opacity: 0.8;
}

.solid-line {
  width: 2px;
  height: 3vh;
  background-color: rgb(213, 213, 213);
}

.icons {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
}

.circle {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 2px solid rgb(209, 209, 209);
}

.start {
  border-style: dashed;
}

.end {
  background-color: var(--color-box-bg);
}

.plane-icon {
  font-size: 14px;
}

.solid-line {
  width: 2px;
  height: 3vh;
  background-color: rgb(213, 213, 213);
}

.flight-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 120px;
  width: 100%;
}

.line-info {
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  align-items: center;
  gap: 5%;
}

.location {
  background-color: var(--primary-color);
  padding: 5px 20px;
  margin: 0;
  border-radius: var(--border-radius-small);
}

.location p {
  margin: 0;
  color: gray;
  font-size: 14px;
}

.main-info-line {
  display: flex;
  align-items: center;
  justify-content: space-between;

  width: 100%;
  padding: var(--margin);
  background-color: var(--color-box-bg);
  border-radius: var(--border-radius-small);
  height: 155px;
  border: 1px solid var(--color-border);
}

.main-info-div {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.main-info-line img {
  width: 64px;
  height: 64px;
  border-radius: calc(var(--border-radius-small) - var(--margin-box) + 2px);
}

.main-info-div p {
  padding-top: var(--margin);
}

.info-line {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.info-line-title {
  font-size: 12px;
}

.info-line-info {
  font-size: 12px;
  font-weight: bold;
}

.details-person-box {
  background-color: var(--color-box-bg);
  padding: var(--padding);
  border-radius: var(--border-radius-small);
  border: 1px solid var(--color-border);
}

.details-hr {
  margin-top: 10px;
  margin-bottom: 10px;
}

.payment-box-completed {
  background-color: rgb(130, 192, 130);
  color: white;
  border-radius: var(--border-radius-small);
  padding: var(--padding);
}

.payment-box-uncompleted {
  background-color: rgb(219, 121, 121);
  color: white;
  border-radius: var(--border-radius-small);
  padding: var(--padding);
}

.payment-img {
  width: 24px;
  filter: brightness(0) invert(1);
}

@media (max-width: 950px) {
  .columns-50 {
    flex-direction: column;
    gap: 0;
  }

  .col-50 {
    width: 100%;
  }
}
