@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap");

body {
  font-family: 'Montserrat', sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1{
  font-size: 22px;
  font-weight: bold;
}

h2{
  font-size: 18px;
  font-weight: bold;
}

h3{
  font-size: 16px;
  font-weight: 600;
}

h4{
  font-size: 14px;
  font-weight: 600;
}

p{
  font-size: 12px;
  color: var(--secondary-color);
}

hr{
  margin: 0px 0px;
  color: #e3e3e353;
}

.page-div{
  margin-left: var(--menu-width);
  padding: var(--margin);
}

.component-title{
  display: flex;
  align-items: left;
  color: var(--secondary-color);
  margin-top: calc(var(--margin) * 2);
  margin-bottom: calc(var(--margin) / 2);
  font-size: 18px;
  font-weight:600;
}

.top-brd{
  height: 120px;
}

a {
  text-decoration: none;
}

.petrol {
  filter: invert(50%) sepia(75%) saturate(391%) hue-rotate(123deg) brightness(88%) contrast(88%);
}

@media (max-width: 950px) {
  .content {
    margin-left: 0;
  }

  .page-div{
    margin-left: 0px;
    padding: var(--margin);
  }

  .top-brd{
    height: 200px;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
