@import url("/src/variables.css");

.sidebar {
  width: var(--menu-width);
  height: 100vh;
  background: var(--secondary-color);
  position: fixed;
  left: 0;
  top: 0;
  padding-top: 20px;
  transition: transform 0.3s ease-in-out;
}

.sidebar .logo {
  text-align: center;
  font-size: 22px;
  font-weight: bold;
  padding: 10px;
  color: var(--color-white);
}

.sidebar .logo strong {
  color: var(--primary-color);
}

.sidebar img {
  width: var(--icon-menu-px);
}

.sidebar ul {
  list-style: none;
  padding: 0;
}

.sidebar ul li a {
  text-decoration: none;
  color: var(--color-white);
  font-size: 16px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.sidebar ul li a {
  color: #ececec;
}

.image-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: var(--margin);
}

.circle-image {
  width: 20%;
  aspect-ratio: 1/1;
  border-radius: 50%;
  overflow: hidden;
  border: 4px solid white;
  background-color: white;
  display: inline-block;
}

.circle-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.text-name {
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-white);
  margin-top: var(--margin-box);
}

.text-info {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: 400;
  color: rgb(177, 177, 177);
  margin-top: 4px;
  margin-bottom: 32px;
}

.hamburger-menu {
  display: none;
  position: fixed;
  top: 10px;
  right: 10px;
  z-index: 1000;
}

.line-left-align {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: var(--gap);
}

.line-left-align-sp-bet {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: var(--border-radius-small);
  margin-inline: var(--margin-box);
  padding: 10px;
  padding-inline: var(--margin);
  background-color: transparent;
  transition: background-color 0.2s ease-in-out;
}

.line-left-align-sp-bet:hover {
  background-color: #325360;
}

.arrow-icon {
  display: flex;
  align-items: center;
  height: 1px;
}

.arrow {
  height: 18px;
  aspect-ratio: 2/1;
  object-fit: contain;
}

.arrow img {
  width: 24px;
}

.arrow-sidebar{
  filter: invert(100%);
}

.submenu-title p {
  margin-left: 5%;
  margin-top: var(--margin);
  margin-bottom: var(--margin-box);
  color: var(--color-white);
  font-size: 12px;
}

.exit-button {
  width: 100%;
  padding: 10px;
  position: absolute;
  bottom: 0px;
  background-color: #ff4d4f;
  color: white;
  border: none;
  cursor: pointer;
  text-align: center;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@media (max-width: 950px) {
  .sidebar {
    display: none;
  }

  .hamburger-menu {
    display: none;
    position: fixed;
    top: 10px;
    right: 10px;
    z-index: 1000;
  }

  .dropdown-menu {
    position: fixed;
    top: 50px;
    right: 10px;
    width: 200px;
    background-color: #1f3645;
    border-radius: 8px;
    box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
    z-index: 999;
    padding: 1rem;
  }

  .dropdown-menu ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }

  .dropdown-menu li {
    margin-bottom: 12px;
  }

  .dropdown-menu li a {
    color: white;
    text-decoration: none;
    font-weight: 500;
  }

  .circle-image {
    width: 32%;
  }

  .image-container {
    margin-top: 4px;
    justify-content: end;
    width: 50px;
    height: 50px;
  }

  .circle-image {
    width: 80%;
  }

  .text-name {
    flex-direction: column;
    justify-content: center;
    align-items: end;
  }

  .text-name p{
    color: #929292;
  }

  .text-info {
    margin-bottom: 8px;
  }
}
