.flex-vertical {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: var(--font-size-details);
  background-color: white;
  height: 70%;
  padding: var(--padding);
  border-radius: var(--border-radius-small);
  gap: 2px;
}

.flex-horizontal {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
}

.flex-horizontal-boxes {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.flex-horizontal-box {
  width: 49%;
}

.chart-div {
  background-color: var(--color-box-bg);
  border-radius: var(--border-radius-small);
  padding: 12px 22px;
  border: 1px solid var(--color-border);
}

.dot-color-1 {
  width: 10px;
  height: 10px;
  background-color: var(--primary-color);
  border-radius: 24px;
}
.dot-color-2 {
  width: 10px;
  height: 10px;
  background-color: var(--secondary-color);
  border-radius: 24px;
}

.dot-color-3 {
  width: 10px;
  height: 10px;
  background-color: var(--color-3);
  border-radius: 24px;
}
.dot-color-4 {
  width: 10px;
  height: 10px;
  background-color: var(--color-4);
  border-radius: 24px;
}

.dot-color-5 {
  width: 10px;
  height: 10px;
  background-color: var(--color-5);
  border-radius: 24px;
}

.find-more {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: var(--margin);
  font-size: 12px;
  color: rgb(190, 190, 190);
}

@media (max-width: 950px) {
  .flex-horizontal-boxes {
    flex-direction: column;
  }

  .flex-horizontal-box {
    width: 100%;
  }
}
