input {
  width: 95%;
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 12px 12px;
  margin-top: calc(var(--margin-box) / 2);
  font-size: 16px;
}

hr {
  margin: 46px 0px;
}

.col-2 {
  display: flex;
  justify-content: start;
  align-items: center;
  gap: var(--margin-box);
  margin-top: 32px;
}

.col {
  width: 100%;
}

.col h4 {
  font-size: 14px;
  color: rgb(92, 92, 92);
}

.descr {
  font-size: 14px;
  color: rgb(159, 159, 159);
}

.save-btn {
  background-color: var(--primary-color);
  border: none;
  padding: 12px 24px;
  color: var(--color-white);
  font-size: 16px;
  border-radius: var(--border-radius-small);
  margin-top: var(--margin);
  cursor: pointer;
  transition: transform 0.3s ease-in-out;
}

.save-btn:hover {
    background-color: #2a8378;
  }
