.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-btn {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 150px;
  background: white;
  color: var(--color-black);
  border: 1px solid #ccc;
  border-radius: var(--border-radius-small);
  padding: var(--margin-box);
  cursor: pointer;
  transition: background 0.3s;
}

.dropdown-btn:hover {
  background: var(--color-box-bg);
  border: 1px solid var(--color-box-bg);
  transition: background 0.5s;
}

.dropdown-icon {
  margin-left: 8px;
  width: 10px;
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 10px;
  background-color: var(--primary-color);
  color: var(--color-white);
  border-radius: var(--border-radius-small);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 180px;
  margin-top: 5px;
  padding: var(--margin-box);
  list-style: none;
  z-index: 1001;
}

.dropdown-item {
  padding: var(--margin-box);
  cursor: pointer;
}

.dropdown-item:hover {
  background: var(--secondary-color);
  border-radius: calc(var(--border-radius-small) - var(--margin-box));
}
