.boxes-div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: var(--gap);
}

.info-box {
  width: 100%;
  background-color: var(--color-box-bg);
  padding: var(--margin);
  border: 1px solid var(--color-border);
  border-radius: var(--border-radius-small);
}

.info-box img {
  width: 44px;
  background-color: var(--color-box-bg);
  border-radius: var(--border-radius-small);
}

.table-header {
  display: flex;
  align-items: center;
  justify-content: space-between;

  padding-inline: 12px;
  padding-block: 8px;
  background-color: var(--secondary-color);
  color: var(--color-white);
  border-radius: var(--border-radius-small);
}

.table-cont {
  margin-top: var(--margin-box);
  padding: var(--margin-box);
  background-color: var(--color-white);
  border-radius: var(--border-radius-small);
}

.table-cont-line {
  display: flex;
  align-items: center;
  justify-content: space-between;

  color: var(--color-black);
  background-color: var(--color-box-bg);

  padding: 12px;
  margin-top: var(--margin-box);
  border-radius: var(--border-radius-small);
  border: 1px solid var(--color-border);
}

.table-cont-line img {
  width: 44px;
  border-radius: var(--border-radius-small);
}

.table-cont-line-main {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: var(--gap);
}

.table-header-img-div {
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.table-header-line-main {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: var(--gap);
}

.table-header-line-main img {
  width: 22px;
}

.reservationid {
  width: 70%;
  display: flex;
  justify-content: space-between;
}

.checkin {
  display: flex;
  justify-content: space-around;
  width: 20%;
}

.nights {
  display: flex;
  justify-content: space-around;
  width: 10%;
}

.guests {
  display: flex;
  justify-content: space-around;
  width: 10%;
}

.payment {
  display: flex;
  justify-content: space-around;
  width: 130px;
}

.payment-status {
  width: 130px;
  padding: var(--margin-box);
  border-radius: 10px;
  display: flex;
  justify-content: space-around;
}

.payment-status h4 {
  color: var(--color-white);
}

.paid {
  background-color: rgb(91, 144, 91);
}

.pending {
  background-color: rgb(186, 119, 119);
}

@media (max-width: 950px) {
  .boxes-div {
    margin-bottom: var(--margin-box);
  }
  .payment {
    display: none;
  }

  .guests {
    display: none;
  }

  .payment-status {
    display: none;
  }

  .nights {
    display: none;
  }
}
